import React from 'react';
import { Disconnected } from 'pages/Disconnected';
import { Time } from 'pages/Time';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Anxious from 'pages/Anxious';
import Cursor from 'components/Cursor';
// import styled from 'styled-components';
import './style.css';
import Home from 'pages/Home';
import About from 'pages/About';
import styled from 'styled-components';
import withNav from 'hoc/withNav';

export const MainContainer = styled.div`
  margin: 4vw 4vw 0 4vw;
  /* margin: 4vw; */
  height: 100%;
`;

const NavDisconnected = withNav(Disconnected);
const NavTime = withNav(Time);
const NavAnxious = withNav(Anxious);

const App = () => {
  return (
    <Router>
      <Cursor />

      <Switch>
        <Route exact path='/me'>
          <Home />
        </Route>
        <Route path='/me/disconnected'>
          <MainContainer>
            <NavDisconnected />
          </MainContainer>
        </Route>
        <Route path='/me/time'>
          <MainContainer>
            <NavTime />
          </MainContainer>
        </Route>
        <Route path='/me/anxious'>
          <MainContainer>
            <NavAnxious />
          </MainContainer>
        </Route>
        <Route path='/me/whoweare'>
          <About />
        </Route>
      </Switch>
    </Router>
  );
};
export default App;
